main{
    background: #0A070B;
    width: 100%;
    height: 800px;
    overflow: hidden;
    padding-top: clamp(10px, 5vw, 50px);

    display: flex;
    flex-direction: column;
    gap: clamp(32px, 5vw, 90px);
}

livecontent{

    display: flex;
    flex-direction: column;
    gap: 40px;
    height: auto;
}

popularcontent{
    display: flex;
    flex-direction: column;
    gap: 40px;
}

@media screen and (max-width: 768px) {
    livecontent , popularcontent{
        gap: 12px;
    }
}