.footer-logo {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
}

.footer-menu-container {
    height: auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.footer-menu-container a {
    padding-left:  clamp(30px, 9vw, 56px);
    padding-right:  clamp(30px, 9vw, 56px);

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(14px, 5vw, 18px);
    line-height: 24px;
    color: #FFFFFF;


    text-align: center;


    list-style: none;
    text-decoration: none;
    white-space: nowrap;

}

.Help-container {
    border: solid #4F4E50;
    border-width: 0px 1px 0px 1px;
}

.List-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    gap: 29px;
    margin: 40px 0px 40px 0px;
}


.footer-social-container ul {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.footer-social-container ul li {
    list-style: none;
    padding: 0px 37px 0px 37px;


}

.footer-social-container h2 {
    white-space: nowrap;
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(14px, 3vw, 18px);
    line-height: 24px;

    color: #807E81;
    cursor: unset;

}

.instagram-social {
    border: solid #4F4E50;
    border-width: 0px 0px 0px 1px;
}

.Rights{
    width: 100%;
    display: flex;
    justify-content: center;
}

.Rights p {
    font-family: 'Gilroy-Regular';
    font-size: clamp(14px, 3vw, 16px);
    font-weight: 400;
    line-height: 24px;

    color: #FFFFFF;
}

