.Liveshowh1 {
    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    display: flex;
    justify-content: space-between;
}

.Liveshowh1 h1 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.LiveShowContent {
    position: relative;
    width: 100%;
    height: auto;
}



.SeeAll {
    cursor: pointer;
    max-width: 74px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
}


.SeeAll h3 {
    cursor: inherit;
    font-family: 'Gilroy-regular';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}

.SeeAllSvg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.ShowItem {
    width: auto;
    height: 99px;

    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 16px;


}


.LiveshowImgContainer {
    min-width: 126px;
    height: 98.61px;
}

.LiveshowImgContainer img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ShowListInner {
    width: 100%;
    height: 99px;
    overflow: hidden;
    position: relative;
}


.ShowList {
    position: absolute;
    z-index: 21;

    width: 100%;

    height: 99px;

    display: flex;
    flex-direction: row;
    justify-content: left;

    gap: 24px;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */

}

.ShowList::-webkit-scrollbar {
    display: none;
}


.LiveShowInfo {
    width: auto;
    height: 99px;

    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: space-between;



}

.LiveShowInfoH1 {
    width: 234px;
    height: 50px;

}

.LiveShowInfoH1 h1 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    overflow: hidden;
    text-overflow: ellipsis;

    cursor: pointer;
    font-family: 'Gilroy-medium';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;

    color: #FFFFFF;
}

.LiveShowInfoH1 h2 {
    font-family: 'Gilroy-regular';
    font-size: 14px;
    line-height: 20px;

    color: #807E81;
}


.LiveShowStatus {
    cursor: pointer;

    width: 73px;
    height: 26px;

    border: 0.7px solid transparent;

    border-radius: 2px;

    background:
        linear-gradient(to right, #0A070B, #0A070B),
        linear-gradient(to right, #F41B3B, #1884F7);
    background-clip: padding-box, border-box;
    background-origin: padding-box, border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px 8px;
    gap: 10px;
}

.LiveShowStatus p {
    width: 57px;
    height: 18px;

    font-family: 'Gilroy-Medium';
    font-size: 14px;
    line-height: 18px;

    color: #F41B3B;
    flex: none;
    order: 0;
    flex-grow: 0;

}

.LiveShowContent:hover #LiveShow-prevbtn {
    opacity: 100%;
}

.LiveShowContent:hover #LiveShow-nextbtn {
    opacity: 100%;
}

#LiveShow-prevbtn {


    opacity: 0%;

    display: flex;
    transition: all 0.3s;
    z-index: 22;
    left: 0;
    top: 0;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 100%;

    align-items: center;
}

#LiveShow-nextbtn {
    opacity: 0%;
    z-index: 22;
    top: 0;
    right: 0;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 100%;

    display: flex;
    align-items: center;
}

#LiveShow-nextbtn:hover {
    cursor: pointer;
    background-color: #63636379;
}

#LiveShow-prevbtn:hover {
    cursor: pointer;
    background-color: #63636379;
}

#LiveShow-nextbtn img {
    width: 30px;
    height: 30px;
}

#LiveShow-prevbtn img {
    width: 30px;
    height: 30px;

    transform: scaleX(-1);
}








@media screen and (max-width: 768px) {

    #LiveShow-prevbtn, #LiveShow-nextbtn {
        display: none;
    }

    .ShowListInner {
        height: auto;
    }

    .LiveShowContent {
        height: 180px;
    }

    .ShowList {
        overflow-x: scroll;
        z-index: 20;
        position: relative;
        height: 200px;
        gap: 16px;
    }

    .ShowItem {
        min-width: inherit;
        max-width: 200px;
        height: 180px;
        gap: 8px;
        flex-direction: column;
    }

    .LiveshowImgContainer {
        width: 160px;
        height: 100px;
    }

    .LiveshowImgContainer img {
        width: 160px;
        height: 99px;
    }

    .LiveShowInfo {
        width: 142px;
        height: 72px;
        gap: 8px;
        padding-left: 0px;

    }

    .LiveShowInfo h1 {
        width: 141px;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
    }

    .LiveShowInfo h2 {
        font-size: 12px;
        line-height: 20px;
    }



    .LiveShowStatus {
        width: 65px;
        height: 24px;
    }


    .LiveShowContent {
        padding-left: 0;

        position: relative;
        width: auto;
        height: auto;
    }

    .Liveshowh1 h1 {
        font-size: 20px;
        line-height: 32px;
    }

    .Liveshowh1 h3 {
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
    }
}