.PopularMoviesh1 {
    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    display: flex;
    justify-content: space-between;
}

.PopularMoviesh1 h1 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.PopularContent{
    position: relative;
    width: 100%;
    height: auto;
}


.PopularInner{
    width: 100%;
    height: 290px;
    overflow: hidden;
    position: relative;

}


.PopularList{
    position: absolute;
    z-index: 21;
    width: 100%;
    height: 290px;

    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 16px;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
.PopularList::-webkit-scrollbar{
    display: none;
}

.PopularItem{
    cursor: pointer;

    position: relative;

    min-width: 214px;
    height: 290px;
}

.PopularItem img{
    width: 100%;
    height: 100%;
    border-radius: 3px;

    object-fit: cover;

}

.PopularItem:hover {
    z-index: 21;
    transition: all 0.6s;
}
.PopularItem:hover .bg-filter{
    opacity: 5%;
}



.bg-filter{
    opacity: 50%;
    -webkit-user-select: none;
    user-select: none;
    z-index: 10;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(172.23deg, #000000 4.94%, rgba(0, 0, 0, 0.35) 45.74%, rgba(0, 0, 0, 0.9) 92.94%);
    mix-blend-mode: multiply;
}

.PopularItemH1{
    position: absolute;


    left: 0;
    z-index: 20;
    bottom: 14px;
    text-align: left;
    padding: 12px;
}


.PopularItemH1 h1{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;

    color: #FFFFFF;

}


@media screen and (max-width: 768px) {
    .PopularInner{
        overflow-x: scroll;
    }

    .PopularItem{
 
        min-width: 101px;
        height: 136px;
    }

    .PopularList {
        overflow-x: scroll;
        z-index: 20;
        position: relative;
        height: 156px;
        gap: 16px;
    }



.PopularItemH1{
    position: absolute;


    left: 0;
    z-index: 20;
    bottom: 6px;
    text-align: left;
    padding: 4px;
}


.PopularItemH1 h1{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 11px;
    line-height: 12px;

    color: #FFFFFF;

}

.PopularMoviesh1 h1 {
    font-size: 20px;
    line-height: 32px;
}

.PopularMoviesh1 h3 {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
}

.PopularItem:hover {
    z-index: 21;
    transition: all 0.6s;

    transform: none;
    
}
.PopularItem:hover .bg-filter{
    opacity: 70%;
}
    
}